<template>
  <div class="container" ref="container">
    <div class="image-container" ref="imageContainer">
      <img alt="Vue logo" src="./assets/profile.jpg" class="image" ref="image" @click="requestGyro">
    </div>
    <small v-if="beta || angleDelta">{{  }}</small>
    <h1>Jónas Grétar Jónasson</h1>
    <small>CV coming soon... (maybe)</small>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data () {
    return {
      beta: null,
      prevBeta: null,
      angleDelta: null
    }
  },
  computed: {
    debugString () {
      return this.beta.toFixed(0) + ' ' + this.angleDelta.toFixed(0)
    }
  },
  methods: {
    requestGyro () {
      const image = this.$refs.image;
      try {
        DeviceOrientationEvent.requestPermission()
          .then(response => {
              if (response == 'granted') {
                window.addEventListener("deviceorientation", (event) => {

                this.prevBeta = this.beta * 1.0
                this.beta = event.beta

                if (Math.abs(this.beta - this.prevBeta) > 2.5) {

                  const angleDelta = (100 / Math.sqrt(Math.pow(event.beta - 90, 2))) + "s";
                  this.angleDelta = angleDelta
                  const animationDuration = (100 / Math.sqrt(Math.pow(event.beta - 90, 2))) + "s";
                  // Apply the spinning animation with the calculated duration
                  image.style.animation = `spin ${animationDuration} linear infinite`;

                  if (angleDelta > 5) {
                    // Calculate the animation duration based on distance (adjust multiplier as needed)
                    const animationDuration = (5 / angleDelta).toFixed(2) + "s";
                    // Apply the spinning animation with the calculated duration
                    image.style.animation = `spin ${animationDuration} linear infinite`;
                  } else {
                    image.style.animation = ``;
                  }
                }
                }, true);
              }
          })
          .catch(console.error)
      } catch {
        console.log("no ori req")
      }
    }
  },
  mounted () {
    const container = this.$refs.container;
    const imageContainer = this.$refs.imageContainer;
    const image = this.$refs.image;

    container.addEventListener("mousemove", (event) => {
      const { left, top, width, height } = imageContainer.getBoundingClientRect();
      const centerX = left + width / 2;
      const centerY = top + height / 2;
      const distance = Math.hypot(event.clientY - centerY, event.clientX - centerX);

      if (distance > 50) {
        // Calculate the animation duration based on distance (adjust multiplier as needed)
        const animationDuration = (100 / distance) + "s";
        // Apply the spinning animation with the calculated duration
        image.style.animation = `spin ${animationDuration} linear infinite`;
      } else {
        image.style.animation = ``;
      }
    });

    container.addEventListener("mouseleave", () => {
      // Remove the spinning animation when the cursor leaves the container
      image.style.animation = "none";
    });
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

/* Define the CSS class for the spinning animation */
.spin {
  transform-origin: center center;
  animation: spin 2s linear infinite;
}

/* Define the keyframes for the spinning animation */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

/* Add some styles to the image container for interactivity */
.image-container {
  align-items: center;
  position: relative;
  width: 200px;
  height: 200px;
  overflow: hidden;
}

/* Add some styles to the image to make it responsive */
.image {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

</style>
